.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

.save-btn {
  float: left;
  color: white;
  margin-bottom: 1rem;
  margin-left: 1rem;
}

img {
    float: left;
    margin: 2rem;
}

li {
    margin: 2rem;
}

.jumbotron {
    max-height: 1rem;
}
.delete-btn {
  float: right;
  color: #e74944;
}

.link {
  color: black;
  margin-left: 2rem;
}
